  }
}

.check input {
  position: absolute;
  top: 2px;
  left: 0;
  z-index: 2;
  opacity: 0;
}

.check input:checked ~ span {
  user-select: none;
}

.check input:checked ~ span:after {
  content: "";
  position: absolute;
  top: 28px;
  left: 28px;
  display: inline-block;
  width: 32px;
  height: 32px;
  background-color: #3A9FB8;
  border-radius: 100%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1670px) {
  .check input:checked ~ span:after {
    width: 24px;
    height: 24px;
  }
}

@media only screen and (max-width: 1050px) {
  .check input:checked ~ span:after {
    width: 16px;
    height: 16px;
  }
}

.check input:disabled ~ span {
  opacity: 1;
}

.check span {
  position: relative;
  padding: 6px 0 10px 88px;
  left: 0;
  top: 0;
  font-size: 36px;
  line-height: 1.2;
