  .check span {
    padding-left: 54px;
    padding-top: 16px;
  }
}

.check span:before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 58px;
  height: 58px;
  background-color: transparent;
  border: 4px solid #7F7F7F;
  cursor: pointer;
  border-radius: 100%;
