@media only screen and (max-width: 1920px) {
  .time {
    font-size: 120px;
  }
}

@media only screen and (max-width: 1670px) {
  .time {
    font-size: 100px;
  }
}

@media only screen and (max-width: 1505px) {
  .time {
    font-size: 80px;
    padding: 15px 40px;
  }
}

@media only screen and (max-width: 1200px) {
  .time {
    font-size: 76px;
    padding: 10px 20px;
  }
}

.separator {
  display: inline-block;
  width: 2px;
  height: 117px;
  transform: translateY(8px);
  background-color: #7F7F7F;
  margin-left: -1px;
}

@media only screen and (max-width: 1920px) {
  .separator {
    height: 90px;
  }
}

@media only screen and (max-width: 1670px) {
  .separator {
    height: 70px;
  }
}

.result {
  display: none;
  margin-bottom: 30px;
}

.result.is-active {
  display: block;
}

.result__pic {
  width: 30%;
  display: inline-block;
  margin-top: 8px;
}

.result__text {
  width: 67%;
}

.result__title {
  font-size: 48px;
  color: #3A9FB8;
  font-family: 'GothaProMed', Arial, sans-serif;
}

@media only screen and (max-width: 1670px) {
  .result__title {
    font-size: 40px;
  }
}

@media only screen and (max-width: 1050px) {
  .result__title {
    font-size: 32px;
  }
}

.result__wrap {
  display: flex;
  justify-content: space-between;
}

.result__desc {
  font-size: 30px;
  font-family: 'GothaProReg', Arial, sans-serif;
}

@media only screen and (max-width: 1670px) {
  .result__desc {
    font-size: 22px;
  }
}

@media only screen and (max-width: 1050px) {
  .result__desc {
    font-size: 16px;
  }
}

.result__footer {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 33%;
}

@media only screen and (max-width: 1220px) {
  .result__footer {
    padding-left: 0;
  }
}

@media only screen and (max-width: 650px) {
  .result__footer {
    flex-wrap: wrap;
  }
}

.result .title__subtitle {
  padding-left: 33%;
}

.share {
  width: 270px;
  min-width: 270px;
  margin-left: 40px;
}

@media only screen and (max-width: 1670px) {
  .share {
    width: 212px;
    min-width: 212px;
    margin-left: 30px;
  }
}

@media only screen and (max-width: 650px) {
  .share {
    margin: 20px 0 0;
  }
}

.share,
.ya-share2,
.ya-share2__container {
  display: inline-block;
  vertical-align: top;
}

.ya-share2__container .ya-share2__item {
  position: relative;
  display: inline-block;
  width: 74px;
  height: 74px;
  vertical-align: top;
  margin: 0 0 0 24px !important;
  border-radius: 100%;
  box-shadow: 0px 5px 16px 0px rgba(0, 0, 0, 0.12);
}

.ya-share2__container .ya-share2__item:first-of-type {
  margin-left: 0 !important;
}

@media only screen and (max-width: 1670px) {
  .ya-share2__container .ya-share2__item {
    width: 60px;
    height: 60px;
    margin: 0 0 0 16px !important;
  }
}

.ya-share2__container .ya-share2__link,
.ya-share2__container .ya-share2__badge {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.ya-share2__container .ya-share2__link:hover,
.ya-share2__container .ya-share2__badge:hover {
  opacity: .8;
}

.ya-share2__container .ya-share2__link .ya-share2__icon,
.ya-share2__container .ya-share2__badge .ya-share2__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 70%;
  height: 70%;
  vertical-align: top;
}

.ya-share2__container .ya-share2__item_service_vkontakte .ya-share2__badge {
  background-color: #6996C7;
}

.ya-share2__container .ya-share2__item_service_facebook .ya-share2__badge {
  background-color: #496194;
}

.ya-share2__container .ya-share2__item_service_odnoklassniki .ya-share2__badge {
  background-color: #FF9C43;
}
